<template>
  <transition name="slide">
    <div class="wrapper">
      <div class="header">
        <div class="icon-wrapper">
          <i class="back" @click="back"></i>
        </div>
        <div class="search-box">
          <search-box @query="onQueryChange" placeholder="请输入日期/关键词"></search-box>
        </div>
      </div>
      <div class="content-wrapper">
        <ul class="tab" ref="tab">
          <li class="tab-item active" @click="selectTab('0')"><span>全部</span></li>
          <li class="tab-item" @click="selectTab('1')"><span>待签收</span></li>
          <li class="tab-item" @click="selectTab('2')"><span>已完成</span></li>
        </ul>
        <scroll class="content">
          <ul class="list">
            <li class="item" v-for="(item,index) in list">
              <div class="left">
                <p>{{item.date}}</p>
                <p>{{item.time}}</p>
              </div>
              <div class="right">
                <div class="order detail-item">
                  <p><label>配送单ID：</label><span>6254233598745</span></p>
                  <p><label></label><span class="red">待签收</span></p>
                </div>
                <div class="price detail-item">
                  <p><label>品种：</label><span class="red">25</span></p>
                  <p><label>总价：</label><span class="red bold">300</span><label>元</label></p>
                </div>
                <p class="btn">
                  <span @click="selectItem(item)">签收</span>
                </p>
              </div>
            </li>
          </ul>
        </scroll>
      </div>
      <router-view></router-view>
    </div>
  </transition>
</template>

<script type="text/ecmascript-6">
  import Scroll from 'base/scroll/scroll'
  import SearchBox from 'base/search-box/search-box'
  import {addClass,removeClass} from 'common/js/dom'

  export default {
    data () {
      return {
        query: '',
        list: [],
        status: '0'//0,1,2
      }
    },
    created () {
      this._getOrderList()
    },
    methods: {
      back() {
        this.$router.push({
          'path': '/purchase'
        })
      },
      _getOrderList() {
        for(let i = 0; i < 10; i++){
          this.list.push({
            date: '2018-02-12',
            time: '15:31:23',
            orderId: '6254233598745',
            num: '25',
            price: '300.00'
          })
        }
      },
      showCategory() {

      },
      selectItem(item) {
        console.log(item)
        this.$router.push({
          path: '/purchase/distributeorder/detail'
        })
      },
      onQueryChange(newQuery) {
        this.query = newQuery
        //dosomething
      },
      selectTab(status){
        this.status = status
        let tab = this.$refs.tab
        let children = tab.children
        for(let i = 0; i < children.length; i++) {
          let child = children[i]
          if(status == i) {
            addClass(child, 'active')
          } else {
            removeClass(child, 'active')
          }
        }
      }
    },
    watch: {
      status(){
        this._getOrderList()
      }
    },
    components: {
      SearchBox,
      Scroll
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"

  .slide-enter-active,.slide-leave-active
    transition all 0.3s
  .slide-enter,.slide-leave-to
    transform translate3d(100%,0,0)

  .wrapper
    background $color-background
    position absolute
    left 0
    right 0
    top 0
    bottom 0
    z-index 1
    .header
      position fixed
      height 44px
      top 0
      left 0
      right 0
      background $color-header-background
      display flex
      justify-content center
      align-items center
      .icon-wrapper
        width 50px
        height 100%
        .back
          position absolute
          background url("~common/image/back.png") no-repeat
          background-size contain
          width 8px
          height 15px
          left 15px
          top 15px
          &:before
            position absolute
            left -10px
            right -10px
            top -10px
            bottom -10px
            content ''
      .search-box
        display flex
        flex 1
        margin-right 33px
    .content-wrapper
      position fixed
      left 0
      right 0
      top 44px
      bottom 0
      background $color-background
      display flex
      flex-direction column
      .tab
        display flex
        height 45px
        border-top 1px solid #E6E6E6
        background #fff
        color #4c4c4c
        font-size $font-size-medium-m
        width 100%
        .tab-item
          display flex
          flex 1
          justify-content center
          height 100%
          span
            display flex
            height 100%
            flex-direction column
            justify-content center
          &.active
            span
              color #eb4747
              position relative
              &:after
                position absolute
                height 2px
                background #eb4747
                bottom 0
                left 0
                right 0
                content ''
      .content
        margin-top 6px
        background #fff
        max-height 100%;
        overflow hidden
        .list
          .item
            height 125px
            display flex
            box-sizing border-box
            border-bottom 1px solid #f2f2f2
            margin-left 10px
            margin-right 10px
            &:last-child
              border none
            .left
              display flex
              flex-direction column
              color #999
              font-size $font-size-small
              padding-top 20px
              width 64px
              margin-right 8px
              p
                margin-top 4px
                width 100%
                text-align center
            .right
              display flex
              flex 1
              flex-direction column
              .detail-item
                height 28px
                display flex
                align-items center
                color #666
                font-size $font-size-medium-m
                .red
                  color #eb4747
                .bold
                  font-size $font-size-medium-x
                  font-weight bold
              .order
                margin-top 10px
                justify-content space-between
              .price
                justify-content space-between
              .btn
                margin-top 16px
                color #eb4747
                display flex
                justify-content flex-end
                span
                  align-items center
                  display flex
                  justify-content center
                  width 70px
                  height 28px
                  box-sizing border-box
                  border 1px solid #f7a6a4
                  border-radius 4px

</style>




